.single{display:flex;gap:25px}@media screen and (max-width: 1200px){.single{flex-direction:column;gap:50px}}.single .view{flex:1}.single .view .content{display:flex;gap:30px;align-items:flex-start;padding:20px;border:2px solid #e9e9e9;box-shadow:-1px 7px 7px 2px #ededed}.single .view .content .top-info{display:flex;align-items:center;gap:20px}.single .view .content .top-info--verified{position:relative}.single .view .content .top-info--verified::after{content:"✔";color:#fff;position:absolute;background:green;width:20px;height:20px;border-radius:50%;padding:3px;font-size:14px;text-align:center;top:0;right:0}.single .view .content .top-info img{display:flex;align-items:center;width:100px;height:100px;border-radius:50%;object-fit:cover}.single .view .content .top-info h1{font-weight:500}.single .view .content .details{font-size:18px}.single .view .content .details .item{margin-bottom:15px}.single .view .content .details .item .item-title{font-weight:600;margin-right:10px;text-transform:capitalize}.single .view hr{width:90%;height:0;border:.5px solid gray;margin:20px 0px}@media screen and (max-width: 768px){.single .view hr{display:none}}.single .view .chart{margin-top:50px;width:80%;height:400px}@media screen and (max-width: 768px){.single .view .chart{display:none}}.single .activities{flex:1}.single .activities h2{margin-bottom:20px}.single .activities ul li{list-style-type:none;position:relative;width:1px;padding-top:50px;background-color:#5b5df4}.single .activities ul li::after{content:"";position:absolute;left:50%;bottom:0;width:10px;height:10px;border-radius:50%;background-color:#5b5df4;transform:translateX(-50%)}.single .activities ul li div{min-width:480px;padding:15px;background-color:rgba(91,93,244,.06)}@media screen and (max-width: 768px){.single .activities ul li div{min-width:250px}}.single .activities ul li div p{margin-bottom:5px}.single .activities ul li div time{font-size:12px}